import React, { useState } from "react";

const App = () => {
  const [persons, setPersons] = useState([{ name: "Arto Hellas" }]);
  const [phones, setPhones] = useState([{ phone: 122 }]);
  const [newName, setNewName] = useState("");
  const [filterName, setFilterName] = useState(false);
  const [newPhone, setNewPhone] = useState("");
  const [newSearchTerm, setNewSearchTerm] = useState("");
  const showData = filterName
    ? persons.filter((item) => {
        return item.name == newSearchTerm;
      })
    : persons;
  return (
    <div>
      <h2>Phonebook</h2>
      <form
        onSubmit={(e) => {
          e.preventDefault();
          setFilterName(true);
        }}
      >
        <input
          value={newSearchTerm}
          onChange={(e) => {
            setNewSearchTerm(e.target.value);
          }}
        />
        <button type="submit">搜索全名</button>
      </form>
      <hr></hr>
      <form
        onSubmit={(e) => {
          const object = persons.find((item) => {
            return item.name == newName;
          });
          if (object) {
            alert(`${object.name} id already added to phonebook`);
            return;
          }
          e.preventDefault();
          setPersons(persons.concat({ name: newName }));
          setNewName("");
          setPhones(phones.concat({ phone: newPhone }));
          setNewPhone("");
        }}
      >
        <div>
          name:{" "}
          <input
            value={newName}
            onChange={(e) => {
              setNewName(e.target.value);
            }}
          />
        </div>
        <div>
          phone:{" "}
          <input
            value={newPhone}
            onChange={(e) => {
              setNewPhone(e.target.value);
            }}
          />
        </div>
        <div>
          <button type="submit">add</button>
        </div>
      </form>
      <h2>名字</h2>
      {showData.length ? (
        showData.map((item) => {
          return <h4>{item.name}</h4>;
        })
      ) : (
        <span style={{ color: "red", fontSize: "16px" }}>为空</span>
      )}
      <h2>电话</h2>
      {phones.map((item) => {
        return <h4>{item.phone}</h4>;
      })}
    </div>
  );
};

export default App;
