import React, { useState } from "react";

// export default class app1 extends Component {
//     render() {
//         return (
//             <div>

//             </div>
//         )
//     }
// }
const Button = (props) => {
  return (
    <button
      onClick={() => {
        props.onClick();
      }}
    ></button>
  );
};
const App = () => {
  // save clicks of each button to own state
  const [good, setGood] = useState(0);
  const [neutral, setNeutral] = useState(0);
  const [bad, setBad] = useState(0);
  const [click, setClick] = useState(0);

  return (
    <div>
      feed
      <Button
        onClick={() => {
          setGood(good + 1);
          setClick(click + 1);
        }}
      ></Button>
      <Button
        onClick={() => {
          setNeutral(neutral + 1);
          setClick(click + 1);
        }}
      ></Button>
      <Button
        onClick={() => {
          setBad(bad + 1);
          setClick(click + 1);
        }}
      ></Button>
      {click ? (
        <div>
          <br />
          statestics
          <br />
          good{good}
          <br />
          neutral{neutral}
          <br />
          bad{bad}
          <br />
          all {good + bad + neutral}
          <br />
          <br />
        </div>
      ) : (
        "no click"
      )}
    </div>
  );
};
export default App;
