import React, { useState } from "react";
var createClass = require("create-react-class");

// const Button = (props) => (
//   <button onClick={props.handClick}>{props.text}</button>
// );
class Button extends React.Component {
  render() {
    return <button onClick={this.props.handClick}>{this.props.text}</button>;
  }
}
const App1 = () => {
  const [value, setValue] = useState(10);
  return (
    <div>
      {value}
      {/* <button
        onClick={() => {
          setValue(value + 1);
        }}
      ></button> */}
      <Button
        handClick={() => {
          setValue(value + 1);
        }}
        text="123"
      ></Button>
    </div>
  );
};

