const Footer = () => {
  const footerStyle = {
    color: "green",
    fontStyle: "italic",
    fontSize: 16,
  };
  return (
    <div style={footerStyle}>
      <br></br>
      <em>Mote app ,Department of Computer Science ,University of HElsinki</em>
    </div>
  );
};
const Note = ({ note, toggleImportance, del }) => {
  const lable = note.important ? "mark not important" : "mark important";

  return (
    <div>
      <li className="note">{note.content}</li>
      <button onClick={toggleImportance}>{lable}</button>
      <button
        onClick={() => {
          del();
        }}
      >
        del
      </button>
    </div>
  );
};

const Notification = ({ message }) => {
  if (message === null) {
    return null;
  }
  return <div className="error">{message}</div>;
};
const App = (props) => {
  // const [notes, setNotes] = useState(props.notes);
  const [notes, setNotes] = useState([]);
  const [newNote, setNewNote] = useState("a new note...");
  const [showAll, setShowAll] = useState(true);
  const [errorMessage, setErrorMessage] = useState("some error happened...");
  console.log("notes", props);
  const addNote = (event) => {
    event.preventDefault();
    const noteObject = {
      content: newNote,
      date: new Date().toISOString(),
      important: Math.random() < 0.5,
      id: notes.length + 1,
    };
    noteService.create(noteObject).then((returnedNote) => {
      if (returnedNote !== "err") {
        console.log("111", 111);
        setNotes(notes.concat(returnedNote));
        setNewNote("");
      }
    });

    // setNotes(notes.concat(noteObject));
    // setNewNote("");
  };
  const handleNoteChange = (event) => {
    console.log(event.target.value);
    setNewNote(event.target.value);
  };
  const notesToShow = showAll
    ? notes
    : notes.filter((note) => note.important === true);
  useEffect(() => {
    console.log("effect");
    noteService.getAll().then((initialNotes) => {
      console.log("promise fulfilled");
      setNotes(initialNotes);
    });
  }, []);
  console.log("notesToShow", notesToShow, notes, props);
  const toggleImportanceOf = (id) => {
    console.log("importance of " + id + " needs to be toggled");
    const url = `http://localhost:3001/notes/${id}`;
    const note = notes.find((n) => n.id === id);
    const changedNote = { ...note, important: !note.important };
    noteService
      .update(id, changedNote)
      .then((returnedNote) => {
        setNotes(
          notes.map((note) => {
            return note.id !== id ? note : returnedNote;
          })
        );
      })
      .catch((error) => {
        setErrorMessage(`Note ${note.content} was already removed from server`);
        setTimeout(() => {
          setErrorMessage(null);
        }, 5000);
        setNotes(notes.filter((n) => n.id !== id));
      });
  };
  return (
    <div>
      <h1>Notes</h1>
      <Notification message={errorMessage} />
      <div>
        <button onClick={() => setShowAll(!showAll)}>
          show {showAll ? "important" : "all"}
        </button>
      </div>
      <ul>
        {notesToShow.map((note, i) => (
          <Note
            key={note.id}
            note={note}
            toggleImportance={() => {
              toggleImportanceOf(note.id);
            }}
            del={() => {
              noteService.del(note.id).then(() => {
                noteService.getAll().then((initialNotes) => {
                  setNotes(initialNotes);
                });
              });
            }}
          />
        ))}
      </ul>
      <form onSubmit={addNote}>
        <input value={newNote} onChange={handleNoteChange} />
        <button type="submit">save</button>
      </form>
      <Footer />
    </div>
  );
};
